import { defineComponent, ref, onMounted } from 'vue';
import ButtonComponent from '../components/ButtonComponent.vue';
import SidebarComponent from '../components/SidebarComponent.vue';
import NavBar from '../components/NavBar.vue';
import { useRouter } from 'vue-router';
export default defineComponent({
    name: "Dashboard",
    components: {
        ButtonComponent,
        SidebarComponent,
        NavBar
    },
    data() {
        return {
            menuItems: [],
            username: "Invité",
            role: "User", // Rôle par défaut
        };
    },
    props: {
        entityType: {
            type: String,
            required: true,
        },
    },
    setup(props) {
        const router = useRouter();
        // Récupérer les données utilisateur depuis localStorage
        const username = ref(localStorage.getItem("name") || "Invité");
        const role = ref(localStorage.getItem("role") || "User");
        // Définir les menus spécifiques à chaque rôle
        const menuItems = ref([
            { name: 'Tableau de Bord', link: '/dashboard', active: true, visibleTo: ['Admin', 'Client', 'Manager', 'Utilisateur'] },
            { name: 'Gestion Utilisateur', link: '/liste', active: false, visibleTo: ['Admin', 'Client', 'Manager'] },
            { name: "Profil d'utilisateur", link: '/profile', active: false, visibleTo: ['Admin', 'Client', 'Manager', 'Utilisateur'] },
        ]);
        // Filtrer les éléments du menu en fonction du rôle de l'utilisateur
        const filteredMenuItems = menuItems.value.filter(item => item.visibleTo.includes(role.value));
        console.log(props.entityType === 'Admin');
        console.log(role.value);
        onMounted(() => {
            if (!username.value || !role.value) {
                // Rediriger vers la page de connexion si les données sont manquantes
                router.push({ name: 'Login' });
            }
        });
        const logout = () => {
            localStorage.removeItem("username");
            localStorage.removeItem("role");
            localStorage.removeItem('token');
            router.push({ name: 'Login' });
        };
        return {
            username,
            role,
            menuItems,
            logout,
            filteredMenuItems
        };
    },
});
